  display: flex;
  flex-direction: column;
}*/

.ManifoldConnect {
  margin-left: 1600px;
  margin-right: 0;
  margin-top: 25px;
  position: absolute;
  z-index: 999;
}

.nftDiv {
  background-color: #282c34;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
